<script>
import { defineComponent } from 'vue';
import { IonPage, IonContent, IonRow, IonCol} from '@ionic/vue';
//import { chevronForwardCircleOutline } from 'ionicons/icons';

//import ExploreContainer from '@/components/ExploreContainer.vue';

export default  defineComponent({
  name: 'Tab1Page',
  components: { IonContent, IonPage, IonRow, IonCol},
  data(){
    return {
      lock:true,
      msg:'',
      i:0,
    }
  },  
  mounted(){
    this.pr();
  },
  methods:{
    pr:function(){
      const txt = 'Here is Chien Yu Lin.          Welcome.          ';
      if (this.i < txt.length) {
        this.msg+= txt.charAt(this.i);
        this.i+=1;
        setTimeout(this.pr, 100);
      }
      else{
        setTimeout(this.pr, 200);
        if(this.msg=='Here is Chien Yu Lin.          Welcome.          '){
          this.msg='';
          this.i = 0;
        }
      }
    }
  }
});
